import React from 'react';
import { Link } from 'gatsby';
import Layout from 'components/Layout';
import { Typography, Box, Button, Divider } from '@mui/material';
import { useTheme } from '@mui/material/styles';

const isEnglishPath = (pathname: string) => {
  const normalized = pathname.replace(/\/+$/, '');
  return normalized.endsWith('/en');
};

const ErrorPage = ({ pathname }: { pathname: string }) => {
  const theme = useTheme();
  const isEnglish = isEnglishPath(pathname);

  const errorContent = isEnglish
    ? {
        title: 'The page could not be found.',
        body: 'This is a quiet detour. Perhaps the link is outdated, or the page has moved elsewhere.',
        cta: 'Home',
        homePath: '/en/',
      }
    : {
        title: '페이지를 찾을 수 없습니다.',
        body: '잠시 길을 잃은 페이지입니다. 링크가 오래되었거나, 글이 다른 위치로 옮겨졌을 수 있어요.',
        cta: '홈으로',
        homePath: '/',
      };

  return (
    <Layout>
      <Box
        display="flex"
        flexDirection="column"
        alignItems="center"
        textAlign="center"
        sx={{
          maxWidth: 640,
          margin: '0 auto',
          paddingTop: theme.spacing(6),
          paddingBottom: theme.spacing(8),
          gap: theme.spacing(2),
        }}
      >
        <Typography
          component="p"
          sx={{
            fontSize: { xs: '3rem', md: '4.5rem' },
            letterSpacing: '0.08em',
            opacity: 0.75,
          }}
        >
          404
        </Typography>
        <Typography component="h1" variant="h4" fontWeight={theme.typography.fontWeightMedium}>
          {errorContent.title}
        </Typography>
        <Typography
          component="p"
          sx={{
            maxWidth: 520,
            color: 'text.secondary',
            lineHeight: 1.9,
          }}
        >
          {errorContent.body}
        </Typography>
        <Divider sx={{ width: 120, marginY: theme.spacing(1) }} />
        <Button
          component={Link}
          to={errorContent.homePath}
          variant="contained"
          color="primary"
          sx={{ marginTop: theme.spacing(2) }}
        >
          {errorContent.cta}
        </Button>
      </Box>
    </Layout>
  );
};

export default ErrorPage;
